<template>
  <div id="app">
    <!-- <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div>
    <router-view/> -->
    <div class="hide" :class="{ active: $store.state.open || $store.state.checkOpen }" ref="hideDiv"></div>

    <div class="order-setting" :class="{ active: $store.state.open }" ref="orderSettingDiv">
      <div class="title">
        <p>訂單資訊</p>
        <a href="#" @click="cancelHandler"><i class="fas fa-times"></i></a>
      </div>
      <div class="content">
        <p>品名: {{ $store.state.checkItem.name }}</p>

        <p>
          數量:<button class="reduce" @click="numSettingHandler($event)">-</button>
          {{ itemNum }}
          <button class="plus" @click="numSettingHandler($event)">
            +
          </button>
        </p>

        <p>單價:{{ $store.state.checkItem.price }}元</p>
        <p>總結:{{ totalPrice }}元</p>
      </div>
      <div class="submit-button">
        <button class="sure" @click="submitHandler">
          確認
        </button>
        <button class="cancel" @click="cancelHandler">取消</button>
      </div>
    </div>

    <div class="error-message" :class="{ active: $store.state.error }" ref="errorMessageDiv">
      <div class="title">
        <p>錯誤訊息</p>
        <a href="#" @click="cancelErrorHandler"><i class="fas fa-times"></i></a>
      </div>
      <div class="content">
        <p>訂單不可為空白</p>
      </div>
      <div class="submit-button">
        <button class="sure" @click="cancelErrorHandler">
          確認
        </button>
      </div>
    </div>

    <div class="checkout-check" :class="{ active: $store.state.checkOpen }" ref="checkoutCheckDiv">
      <div class="title">
        <p>結帳</p>
        <a href="#" @click="cancelCheckHandler"><i class="fas fa-times"></i></a>
      </div>
      <div class="content">
        <p>確認送出訂單?</p>
      </div>
      <div class="submit-button">
        <button class="sure" @click="finishOrderHandler">
          確認
        </button>
        <button class="cancel" @click="cancelCheckHandler">取消</button>
      </div>
    </div>

    <header class="header" :class="{ active: $store.state.isActive }">
      <div class="right-button" :class="{ active: $store.state.isActive }" @click="$store.commit('CLICK_HANDLER')">
        <div class="hamburger-bar"></div>
      </div>
    </header>

    <!-- sidebar -->
    <div class="sidebar" :class="{ active: $store.state.isActive }">
