        <div class="logo">
          <!-- <img src="https://picsum.photos/50/50/?random=1" /> -->
          <h3>
            快點餐吧
          </h3>
        </div>
        <ul class="nav-list">
          <li>
            <router-link class="link" :to="{ name: 'Juice', query: { filter: 'juice' } }" replace>
              <i class="fas fa-edit"></i>
              <span class="links-name">點餐</span>
            </router-link>
          </li>
          <li>
            <router-link to="/order-view" class="link">
              <i class="fas fa-list-alt"></i>
              <span class="links-name">訂單</span>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <router-view />
  </div>
