<script>
// @ is an alias to /src

export default {
  data() {
    return {
      itemNum: 1,
      item: [],
    };
  },
  created() {
    window.addEventListener('scroll', this.settingHeight);
    window.localStorage.setItem('order-list', '[]');
    window.localStorage.setItem('order-item', '[]');
  },
  destroyed() {
    window.removeEventListener('scroll', this.settingHeight);
  },
  methods: {
    submitHandler() {
      this.$store.dispatch('ADD_ORDER', {
        name: this.$store.state.checkItem.name,
        num: this.itemNum,
        price: this.$store.state.checkItem.price,
        totalPrice: this.totalPrice,
      });
      this.cancelHandler();
    },

    cancelHandler() {
      this.$store.state.open = false;
      document.body.style.overflowY = '';
      this.itemNum = 1;
    },

    finishOrderHandler() {
      this.$store.dispatch('FINISH_ORDER_LIST');
      this.$store.commit('RESET_COUPON_CODE_ITEM');
      this.cancelCheckHandler();
    },

    cancelCheckHandler() {
      this.$store.state.checkOpen = false;
      document.body.style.overflowY = '';
    },
    cancelErrorHandler() {
      this.$store.state.error = false;
      document.body.style.overflowY = '';
    },
    numSettingHandler(e) {
      if (e.currentTarget.className === 'reduce' && this.itemNum > 1) {
        this.itemNum--;
      } else if (e.currentTarget.className === 'plus') {
        this.itemNum++;
      } else {
        return;
      }
    },
    settingHeight() {
      this.$refs.hideDiv.style.height = `calc(100vh + ${document.documentElement.scrollTop}px`;

      this.$refs.orderSettingDiv.style.top = `calc(30vh + ${document.documentElement.scrollTop}px`;

      this.$refs.checkoutCheckDiv.style.top = `calc(30vh + ${document.documentElement.scrollTop}px`;

      this.$refs.errorMessageDiv.style.top = `calc(30vh + ${document.documentElement.scrollTop}px`;
    },
  },
  computed: {
    totalPrice() {
      return this.$store.state.checkItem.price * this.itemNum;
    },
  },
};
</script>

