import Vue from 'vue';
import VueRouter from 'vue-router';
// import Home from '../views/Home.vue';
import Order from '../views/Order.vue';
import Juice from '../views/JuiceViews.vue';
import Milk from '../views/MilkViews.vue';
// import HelloWorld from '../views/JuiceViewsCopy.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Order',
    component: Order,
    redirect: '/juice',
    children: [
      { path: 'juice', name: 'Juice', component: Juice },
      { path: 'milk', name: 'Milk', component: Milk },
    ],
  },
  {
    path: '/order-view',
    name: 'OrderView',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/OrderView.vue'),
  },
];

const router = new VueRouter({
  // mode: 'history',
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
});

export default router;
