<template>
  <div class="pos">
    <!-- main container -->
    <div class="main-container" :class="{ active: $store.state.isActive }">
      <div class="container">
        <ul>
          <router-link
            :class="{ 'router-link-exact-active': $route.path === '/juice' }"
            :to="{ name: 'Juice', query: { filter: 'juice' } }"
            replace
          >
            <li>
              <a href="">果汁</a>
            </li>
          </router-link>
          <router-link :to="{ name: 'Milk', query: { filter: 'milk' } }" replace>
            <li>
              <a href="">鮮乳</a>
            </li>
          </router-link>
        </ul>
        <router-view />
      </div>

      <!-- Checkout -->
      <div class="checkout">
        <div class="checkout-container">
          <div class="title">訂單詳情</div>

          <div class="description">
            <div class="name">品名</div>
            <div class="num">數量</div>
            <div class="price">單價</div>
            <div class="total">總結</div>
          </div>

          <div class="list" v-for="item of newList" :key="item.oId">
            <i class="fas fa-trash-alt" @click="deleteHandler(item.oId)"></i>
            <div class="name">
              {{ item.order.name }}
            </div>
            <div class="number">{{ item.order.num }}</div>
            <div class="price">{{ item.order.price }}</div>
            <div class="total">{{ item.order.totalPrice }}</div>
          </div>
        </div>
        <!-- coupon -->
        <div class="coupon">
          <div class="coupon-code">
            優惠代碼:
            <input type="text" v-model="couponCode" v-on:keyup.enter="couponHandler" />
            <button @click="couponHandler">確認</button>
          </div>
        </div>
        <!-- total -->
        <div class="discount">折扣 {{ $store.state.discount }} 元</div>
        <div class="total-price">
          總共 {{ $store.state.success ? $store.state.discountTotalPrice : $store.state.allTotalPrice }} 元
        </div>
        <div class="accounts">
          <button @click="checkOpenHandler">結帳</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      allTotalPrice: 0,
      couponCode: '',
      // discountTotalPrice: 0,
      // discount: 0,
      // oldPrice: 0,
      // success: false,
      list: {},
    };
  },
  created() {
    // beforeunload事件在頁面重新整理時先觸發
    window.addEventListener('load', () => {
      this.$store.dispatch('READ_ORDER');
    });
    // 重新載入畫面時，呼叫READ_ORDER，把資料載回來，避免因為重新整理，而使資料無法正常顯示在畫面上
  },

  watch: {
    list: {
      handler: function() {
        this.allTotalPrice = 0;
        return this.list.forEach((item) => {
          this.allTotalPrice += item.order.totalPrice;
          this.$store.commit('SET_ORIGINAL_PRICE', this.allTotalPrice);
          if (this.$store.state.success) {
            this.$store.dispatch('COUPON_CODE');
          }
        });
        // 當list變動，就重新計算一次總價
      },
    },
  },
  computed: {
    newList() {
      return (this.list = this.$store.getters.list);
    },
  },
  methods: {
    deleteHandler(oId) {
      this.$store.dispatch('DELETE_ITEM', {
        oId,
      });
      // 刪除該品項
    },

    couponHandler() {
      this.oldPrice = this.allTotalPrice;
      this.$store.commit('SET_COUPON_CODE', this.couponCode);
      // this.$store.commit('SET_ORIGINAL_PRICE', [this.allTotalPrice, this.couponCode]);
      this.$store.dispatch('COUPON_CODE');
      // if (this.couponCode === 'VIP666') {
      //   this.success = true;
      //   this.discountTotalPrice = this.allTotalPrice * 0.8;
      //   this.discount = this.oldPrice - this.discountTotalPrice;
      // } else {
      //   this.success = false;
      //   this.discount = 0;
      // }
      this.couponCode = '';
      // 優惠券套用，如果優惠碼較多，則應該以陣列的方式做管理會比較好
    },

    checkOpenHandler() {
      if (this.$store.state.orderList.length <= 0) {
        this.$store.state.error = !this.$store.state.error;
      } else {
        this.$store.state.checkOpen = !this.$store.state.checkOpen;
        //暫時處理
        // this.success = false;
        // this.discount = 0;
        // this.couponCode = '';
        //
      }
      document.body.style.overflowY = 'hidden';
    },
  },
};
</script>

