import Vue from 'vue';
import Vuex from 'vuex';
import LocalStorage from '../modules/localStorage';

Vue.use(Vuex);

const STORE = new LocalStorage('order-item');
const ORDER_LIST = new LocalStorage('order-list');

export default new Vuex.Store({
  state: {
    drinks: [
      {
        name: '檸檬多多',
        price: 60,
        type: 'juice',
        imgSrc: 'http://milu101340509304.weebly.com/uploads/1/1/2/4/112486559/s140353479226229518_p1_i1_w200.jpeg',
      },
      {
        name: '草莓芝芝',
        price: 120,
        type: 'juice',
        imgSrc: 'http://img.wangye.cn/uploads/allimg/200415/106-200415135937.jpg',
      },
      {
        name: '楊枝甘露',
        price: 90,
        type: 'juice',
        imgSrc: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTonsvHKuFOVa73evBe6JhVZ2o1HIGHPRvISQ&usqp=CAU',
      },
      {
        name: '芭樂檸檬',
        price: 60,
        type: 'juice',
        imgSrc: 'https://cf.shopee.tw/file/f6a636d82dd95787cb1187f9b5e6aa0a',
      },
      {
        name: '百香雙響砲',
        price: 50,
        type: 'juice',
        imgSrc: 'https://dtvtjl5au16ai.cloudfront.net/wp-content/uploads/2020/05/43.jpg',
      },
      {
        name: '柳橙翡翠',
        price: 60,
        type: 'juice',
        imgSrc: 'https://images.chinatimes.com/newsphoto/2020-09-24/900/20200924002867.jpg',
      },
      {
        name: '水果茶',
        price: 70,
        type: 'juice',
        imgSrc: 'https://cdn2.ettoday.net/images/3940/3940890.jpg',
      },
      {
        name: '甘蔗青茶',
        price: 55,
        type: 'juice',
        imgSrc: 'https://disk.sharelife.tw/gallery/59175-92707/7f68291220220417.jpg',
      },
      {
        name: '焙紅鮮乳茶那堤',
        price: 65,
        type: 'milk',
        imgSrc: 'https://d1ralsognjng37.cloudfront.net/65f02db1-48fe-4f0d-af5d-dfb5b5d96abf.jpeg',
      },
      {
        name: '可可鮮乳茶那堤',
        price: 75,
        type: 'milk',
        imgSrc: 'https://d1ralsognjng37.cloudfront.net/dd6e63ea-d63a-4cc4-b60d-738a8baab717.jpeg',
      },
      {
        name: '鮮乳抹茶那堤',
        price: 75,
        type: 'milk',
        imgSrc: 'https://img.ltn.com.tw/Upload/food/page/2016/11/22/161122-4951-8-LuM9A.jpg',
      },
      {
        name: '龜記珍珠濃乳茶',
        price: 70,
        type: 'milk',
        imgSrc: 'https://i.beauty321.com/816x/https://il.beauty321.com/gallery/gallery/41970/photo-60eef9204f66d.jpg',
      },
      {
        name: '黑糖珍珠鮮奶茶',
        price: 75,
        type: 'milk',
        imgSrc:
          'https://1.bp.blogspot.com/-lsHjQf682MM/WvGYI0j7rqI/AAAAAAAACm8/f_moQ7ecmwQMBgpKVIo9HRHExP0PTKfrgCLcBGAs/s1600/1.jpg',
      },
    ],
    orderList: [],
    finishOrderList: [],
    checkItem: [],
    isActive: true,
    open: false,
    checkOpen: false,
    error: false,

    allTotalPrice: 0,
    discountTotalPrice: 0,
    discount: 0,
    originalPrice: 0,
    success: false,
    couponCode: '',
  },
  getters: {
    list(state) {
      return state.orderList.map((order, index) => {
        return {
          oId: index,
          order,
        };
      });
    },
    finishList(state) {
      return state.finishOrderList.map((order, index) => {
        return {
          oId: index,
          order,
        };
      });
    },
    drinksFilter(state, getters) {
      return function(filter) {
        switch (filter) {
          case 'all':
            return state.drinks;
          case 'juice':
            return state.drinks.filter((drink) => {
              return drink.type === 'juice';
            });
          case 'milk':
            return state.drinks.filter((drink) => {
              return drink.type === 'milk';
            });
          default:
            return [];
        }
      };
    },
  },
  mutations: {
    CLICK_HANDLER(state) {
      state.isActive = !state.isActive;
    },
    SET_ORDER(state, order) {
      state.orderList = order;
    },
    SET_ORDER_LIST(state, orderList) {
      state.finishOrderList = orderList;
    },
    SET_ORIGINAL_PRICE(state, allTotalPrice) {
      state.originalPrice = allTotalPrice;
      state.allTotalPrice = allTotalPrice;
    },
    SET_COUPON_CODE(state, couponCode) {
      state.couponCode = couponCode;
    },
    SET_COUPON_SUCCESS(state) {
      state.success = true;
      state.discountTotalPrice = state.allTotalPrice * 0.8;
      state.discount = state.originalPrice - state.discountTotalPrice;
    },
    SET_COUPON_FAIL(state) {
      state.success = false;
      state.discount = 0;
    },
    RESET_COUPON_CODE(state) {
      state.couponCode = '';
    },
    RESET_COUPON_CODE_ITEM(state) {
      state.success = false;
      state.discount = 0;
      state.couponCode = '';
      state.allTotalPrice = 0;
    },
  },
  actions: {
    COUPON_CODE({ state, commit }) {
      if (state.couponCode === 'VIP666' || state.success) {
        commit('SET_COUPON_SUCCESS');
      } else {
        commit('SET_COUPON_FAIL');
      }
      commit('RESET_COUPON_CODE');
    },

    ADD_ORDER({ commit }, { name, num, price, totalPrice }) {
      const order = STORE.load();
      order.push({ name, num, price, totalPrice });
      STORE.save(order);
      // 2. commit 呼叫mutations
      commit('SET_ORDER', order);
      // 3. return
      return {
        oId: order.length - 1,
        name,
        num,
        price,
        totalPrice,
      };
    },

    READ_ORDER({ commit }) {
      // 1.load/(API:axios.get())
      const order = STORE.load();
      // 2.commit mutations
      commit('SET_ORDER', order);
      // 3.return
      return {
        order,
      };
    },

    READ_ORDER_LIST({ commit }) {
      // 1.load/(API:axios.get())
      const orderList = ORDER_LIST.load();

      // 2.commit mutations
      commit('SET_ORDER_LIST', orderList);
      // 3.return
      return {
        orderList,
      };
    },

    UPDATE_ITEM({ commit }, { oId, orderItem }) {
      // 1.load/(API:axios.patch())
      const order = STORE.load();
      order.splice(oId, 1, orderItem);
      // let oId = new Date().getTime();
      STORE.save(order);
      // 2.commit mutations
      commit('SET_ORDER', order);
      // 3.return
      return {
        oId,
        orderItem: order[oId],
      };
    },

    DELETE_ITEM({ commit }, { oId }) {
      // 1.load/(API:axios.delete())
      const order = STORE.load();
      let deleteOrder = order.splice(oId, 1)[0];
      STORE.save(order);
      // 2.commit mutations
      commit('SET_ORDER', order);
      // 3.return
      return {
        oId: null,
        deleteOrder,
      };
    },

    CLEAR_ORDER({ commit }) {
      STORE.save([]);
      commit('SET_ORDER', []);
    },

    CLEAR_ORDER_LIST({ commit }) {
      ORDER_LIST.save([]);
      commit('SET_ORDER_LIST', []);
    },

    FINISH_ORDER_LIST({ dispatch, commit, state }) {
      // 1.load/(API:axios.post())
      let total = 0;
      const order = STORE.load();
      total = state.discountTotalPrice || state.allTotalPrice;
      console.log();

      const orderList = ORDER_LIST.load();

      orderList.push({ order, total });
      ORDER_LIST.save(orderList);
      // 2.commit mutations

      dispatch('CLEAR_ORDER');
      commit('SET_ORDER_LIST', orderList);
      // 3.return
      return {
        oId: orderList.length - 1,
        orderList,
      };
    },
  },
});
