<template>
  <div class="juice">
    <div class="order">
      <div class="order-item" v-for="item of list" :key="item.name" @click="orderSettingHandler(item)">
        <div class="img">
          <img :src="item.imgSrc" alt="飲料圖片" />
        </div>
        <div class="txt">
          <div class="title">{{ item.name }}</div>
          <div class="pricr">{{ item.price }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Juice',
  data() {
    return {
      filter: 'milk',
    };
  },
  watch: {
    $route: {
      // immediate 是否component一進來就執行
      immediate: true,
      handler: function(route) {
        this.filter = route.query.filter || 'milk';
      },
    },
  },
  methods: {
    orderSettingHandler(item) {
      this.$store.state.open = !this.$store.state.open;
      document.body.style.overflowY = 'hidden';
      this.$store.state.checkItem = item;
    },
  },
  computed: {
    list() {
      return this.$store.getters.drinksFilter(this.filter);
    },
  },
};
</script>

